@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Quicksand&display=swap);
body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

img {
  box-shadow: 5px 5px 8px #333333;
  width: 600px;
  height: auto;
  object-fit: cover;
}



.project-container {
  font-family: 'Open Sans', sans-serif;
}

.project-row {
  display: -webkit-flex;
  display: flex;
  margin: 1em;
} 

.project-row > div {
  padding: 2em;
} 

.project-container .project-row:nth-child(2n+1) {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  border-radius: 1em;
  background-color: #ebebeb;
  color: #4d4d4d;
}


.App {
  
}

.header-container {
  display: -webkit-flex;
  display: flex;
  padding: 0 .75em;
  /* border-bottom: .5px solid #333333; */
  margin-bottom: 1em;
}

.header-h1, .header-contact, .header-title {
  font-family: 'Quicksand', sans-serif;
}

.header-h1 {
  margin-right: auto;
}

.header-title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
}

.header-contact {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-left: auto;
}

ul {
  list-style-type: none;
}

li {
  margin: 0 1em;
}


.App-header {
  background-color: #3a6ea5;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



